import React from "react"
import { createUseStyles } from "react-jss"
import Img from "gatsby-image"
import { Dialog, DialogOverlay, DialogContent } from "@reach/dialog"
import "@reach/dialog/styles.css"

const LightBox = props => {
  const { showDialog, setShowDialog, imageList, activePhotoIndex } = props
  const classes = useStyles()

  if (!showDialog) {
    return null
  }

  return (
    <DialogOverlay
      style={{ background: "rgba(0, 0, 0, 0.99)" }}
      isOpen={showDialog}
      onDismiss={() => setShowDialog(false)}
    >
      <DialogContent
        className={classes.dialogContent}
        // style={{ boxShadow: "0px 10px 50px hsla(0, 0%, 0%, 0.33)" }}
      >
        <div className={classes.imageContainer}>
          <Img
            fluid={imageList[activePhotoIndex].node.childImageSharp.fluid}
            imgStyle={{ objectFit: "CONTAIN" }}
            style={{
              width: `min(${
                imageList[activePhotoIndex].node.childImageSharp.fluid
                  .aspectRatio * 60
              }vh, 100vw)`,
            }}
          />
          <div className={classes.modalName}>Testing Title</div>
        </div>
      </DialogContent>
      <div className={classes.modalName} onClick={e => {
        //   Prevent Outer div from being clicked
          e.stopPropagation()
          console.log("hi")
      }}>TEST</div>
    </DialogOverlay>
  )
}

const useStyles = createUseStyles({
  imageContainer: {
    display: "flex",
    flexFlow: "column",
    // width: "40vw",
    // height: "40vh",
    /* background-color: #e0e0e0; */
    alignItems: "center",
    justifyContent: "center",
  },
  dialogContent:{
      width: "fit-content",
      padding: "0px",
      background: "none"
  },
  modalName: {
    color: "white",
    display: "flex"
  },

  test: {
    maxWidth: "100%",
    maxHeight: "100%",
    height: "auto",
    width: "auto",
  },
  root: {
    display: "flex",
    // flexGrow: 1,
    // width: "100vw",
    width: "60vw",
    height: "60vh",
    alignItems: "center",
    justifyContent: "center",
    // height: "100vh",
    backgroundColor: "#000",
  },
})

export default LightBox
